#root {
  display: grid;
  grid-template-columns: repeat(12, 8%);
  grid-template-rows: repeat(27, 100px);
  background-color: rgb(34, 52, 34);
}

@media screen and (max-width: 1600px) {
  #root {
    grid-template-rows: repeat(27, 80px);
  }
}
@media screen and (max-width: 1200px) {
  #root {
    grid-template-rows: repeat(27, 60px);
  }
}

@media screen and (max-width: 800px) {
  #root {
    grid-template-rows: repeat(27, 40px);
  }
}

@media screen and (max-width: 400px) {
  #root {
    grid-template-rows: repeat(27, 30px);
  }
}

header {
  justify-self: center;
  background-color: rgb(234, 225, 196);
  width: 97%;
  height: 97%;
  border-radius: 25px;
  grid-area: 1 / 1 / 3 / 13;
  transition: transform 2s ease;
}

@keyframes headerexpand {
  0% {
    transform: scale(0) translateX(0);
  }
  50% {
    transform: scale(0) translateX(0);
  }
  100% {
    transform: scale(1) translateX(0) translateY(0);
  }
}
header {
  animation: expand2 2s ease-out 1s forwards;
}

footer {
  justify-self: center;
  background-color: rgb(234, 225, 196);
  width: 97%;
  height: 97%;
  border-radius: 25px;
  grid-area: 25 / 1 / 27 / 13;
  transition: transform 2s ease;
}

@keyframes footerexpand {
  0% {
    transform: scale(0) translateX(0);
  }
  50% {
    transform: scale(0) translateX(0);
  }
  100% {
    transform: scale(1) translateX(0) translateY(0);
  }
}

footer {
  animation: expand2 2s ease-out 1s forwards;
}

article {
  display: grid;
}

img {
  border-radius: 25px;
  justify-self: center;
  align-self: center;
  object-fit: cover;
  width: 95%;
  height: 95%;
}

.centerimage {
  grid-area: 3 / 6 / 7 / 10;
  z-index: 3;
  transition: transform 2s ease;
}

@keyframes shrinkAndMove {
  0% {
    transform: scale(1.2) translateX(-100px);
  }
  50% {
    transform: scale(1) translateX(-100px);
  }
  100% {
    transform: scale(1) translateX(0);
  }
}

.centerimage {
  animation: shrinkAndMove 2s ease-out 1s forwards;
}

